.Cases {
    position: relative;
    background-color: black;
    width: 100%;
    height: 3050px;
    &_Arrow {
        position: absolute;
        width: 140px;
        padding: 48px 28px ;
        background-color: #d0b5b5;
        top: 2767px;
        left: 71.875%;
        &:before {
            position: absolute;
            top: -830px;
            left: 0px;
            content: '';
            display: block;
            height: 830px;
            width: 2px;
            background: white;
        }
        &:after {
            position: absolute;
            top: -1398px;
            right: 0px;
            content: '';
            display: block;
            height: 1398px;
            width: 2px;
            background: white;
        }
    }
}


